"use client";

import Image from "next/image";
import { Menu, User } from "lucide-react"; // ícones modernos
import { useState } from "react";

export default function Navbar() {
  const [open, setOpen] = useState(false);

  return (
    <nav className="flex items-center justify-between px-4 py-3 bg-white border-b border-zinc-200 shadow-sm dark:bg-zinc-900 dark:border-zinc-800">
      {/* LOGO - alinhado à esquerda */}
      <div className="flex items-center gap-2">
        <Image src="/next.svg" alt="Logo" width={32} height={32} />
        <span className="text-lg font-semibold text-zinc-800 dark:text-zinc-100">
          Brand
        </span>
      </div>

      {/* MENU CENTRAL */}
      <ul className="hidden md:flex items-center gap-8 text-zinc-700 dark:text-zinc-300 font-medium">
        <li className="hover:text-blue-600 cursor-pointer">Início</li>
        <li className="hover:text-blue-600 cursor-pointer">Produtos</li>
        <li className="hover:text-blue-600 cursor-pointer">Serviços</li>
        <li className="hover:text-blue-600 cursor-pointer">Contato</li>
        <li className="hover:text-blue-600 cursor-pointer">Sobre</li>
      </ul>

      {/* ÍCONE À DIREITA */}
      <div className="flex items-center gap-4">
        <User className="w-6 h-6 text-zinc-700 dark:text-zinc-200 cursor-pointer hover:text-blue-600" />
        {/* Ícone hambúrguer - aparece apenas no mobile */}
        <button
          className="md:hidden text-zinc-700 dark:text-zinc-200"
          onClick={() => setOpen(!open)}
        >
          <Menu className="w-6 h-6" />
        </button>
      </div>

      {/* MENU MOBILE */}
      {open && (
        <ul className="absolute top-14 left-0 w-full bg-white dark:bg-zinc-900 border-t border-zinc-200 dark:border-zinc-800 flex flex-col items-center gap-4 py-4 text-zinc-700 dark:text-zinc-300 md:hidden">
          <li className="hover:text-blue-600 cursor-pointer">Início</li>
          <li className="hover:text-blue-600 cursor-pointer">Produtos</li>
          <li className="hover:text-blue-600 cursor-pointer">Serviços</li>
          <li className="hover:text-blue-600 cursor-pointer">Contato</li>
          <li className="hover:text-blue-600 cursor-pointer">Sobre</li>
        </ul>
      )}
    </nav>
  );
}
